// stylelint-disable scss/dollar-variable-empty-line-before

// COLORS
$color-black: #282a33;
$color-white: #fafafa;
$color-gray: #828282;
$color-link: #2f80ed;

// FONTS
$font-primary:
	"Redacted Script",
	"SF Pro Display",
	-apple-system,
	blinkmacsystemfont,
	"Segoe UI",
	roboto,
	oxygen,
	ubuntu,
	cantarell,
	"Open Sans",
	"Helvetica Neue",
	sans-serif;
$line-height-primary: 140%;
$line-height-normal: normal;

// EASING TRANSITIONS
$duration-normal: 0.5s;
$easing-back-out: cubic-bezier(0.25, 0.74, 0.22, 1.1); // http://cubic-bezier.com/#.25,.74,.22,1.1
$easing-back-in: cubic-bezier(0.47, -0.1, 0.5, -0.06); // http://cubic-bezier.com/#.47,-0.1,.5,-.06
$easing-out: ease-out;
$easing-in: ease-in;

// Settings
$desktop: 1025;
$desktop-xl: 1800;
$laptop: 1440;
$tablet: 768;
$mobile: 320;
$desktop-vh: 750;
