$urlRedactedScript: "./fonts/redactedScript/RedactedScript-";
$urlFlowCircular: "./fonts/flowCircular/FlowCircular-Regular";
$urlSFProDisplay: "../fonts/sf-pro-display/SFProDisplay-";

/**
* RedactedScript
*/
// @include font-face($urlFlowCircular, "Flow Circular", normal, normal);
// @include font-face($urlFlowCircular, "Flow Circular", 400, normal);

/**
* RedactedScript
*/
@include font-face($urlRedactedScript + "Bold", "Redacted Script", bold, normal);
@include font-face($urlRedactedScript + "Bold", "Redacted Script", 700, normal);
@include font-face($urlRedactedScript + "Regular", "Redacted Script", normal, normal);
@include font-face($urlRedactedScript + "Regular", "Redacted Script", 400, normal);
@include font-face($urlRedactedScript + "Light", "Redacted Script", 300, normal);

/**
* SFProDisplay
*/
@include font-face($urlSFProDisplay + "Bold", "SF Pro Display", bold, normal);
@include font-face($urlSFProDisplay + "Heavy", "SF Pro Display", 900, normal);
@include font-face($urlSFProDisplay + "Regular", "SF Pro Display", normal, normal);
@include font-face($urlSFProDisplay + "Semibold", "SF Pro Display", 600, normal);
@include font-face($urlSFProDisplay + "Medium", "SF Pro Display", 500, normal);
@include font-face($urlSFProDisplay + "Light", "SF Pro Display", 200, normal);
@include font-face($urlSFProDisplay + "Thin", "SF Pro Display", 100, normal);
@include font-face($urlSFProDisplay + "BoldItalic", "SF Pro Display", bold, italic);
@include font-face($urlSFProDisplay + "BlackItalic", "SF Pro Display", 900, italic);
@include font-face($urlSFProDisplay + "RegularItalic", "SF Pro Display", normal, italic);
@include font-face($urlSFProDisplay + "SemiboldItalic", "SF Pro Display", 600, italic);
@include font-face($urlSFProDisplay + "MediumItalic", "SF Pro Display", 500, italic);
@include font-face($urlSFProDisplay + "LightItalic", "SF Pro Display", 200, italic);
@include font-face($urlSFProDisplay + "ThinItalic", "SF Pro Display", 100, italic);
